@import '../utils/variables';

body {
    font-family: $primary-font;
    font-size: 1.6rem;
    line-height: 1.5;
}

a {
    text-decoration: none;
}

h1, h2, h3, h4 {
    font-family: $secondary-font;
    line-height: 1.2;
    font-weight: 400;
}
h1 {
    font-size: 4.8rem;
}
h2 {
    font-size: 4rem;
}
h3 {
    font-size: 3.2rem;
}
h4 {
    font-size: 2.8rem;
}