@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&family=Sedan:ital@0;1&display=swap");
html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}

ul, li {
  list-style: none;
}

img {
  max-width: 100%;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4 {
  font-family: "Sedan", serif;
  line-height: 1.2;
  font-weight: 400;
}

h1 {
  font-size: 4.8rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3.2rem;
}

h4 {
  font-size: 2.8rem;
}

.header {
  background-color: #f4f6f2;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  background-color: white;
  height: 8rem;
}

.titulo {
  font-family: var(--fuente-principal);
  font-size: 3rem;
  text-transform: uppercase;
  margin: 1rem;
  margin-left: 3rem;
}

.sub-titulo {
  font-family: var(--fuente-principal);
  font-size: 1.5rem;
  margin-right: 1rem;
}

.logo {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.nav {
  display: flex;
  justify-content: flex-end;
  margin-right: 2rem;
}

.nav a {
  color: var(--negro);
}

.nav ul li {
  display: inline-block;
  margin-right: 1rem;
}

.nav ul li:hover {
  font-size: 1.7rem;
  transition: 0.3s;
}

@media (max-width: 880px) {
  .header {
    height: 12rem;
    flex-direction: column;
  }
  .nav {
    padding: 1rem 0;
  }
}
@media (max-width: 550px) {
  .header {
    height: 25rem;
    justify-content: center;
  }
  .logo {
    flex-direction: column;
  }
  .nav ul {
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-top: 2rem;
    margin-left: 3rem;
  }
}